import * as React from 'react';
import PropTypes from 'prop-types';
import Button from '@mui/material/Button';
import { styled } from '@mui/material/styles';
import Dialog from '@mui/material/Dialog';
import DialogTitle from '@mui/material/DialogTitle';
import DialogContent from '@mui/material/DialogContent';
import DialogActions from '@mui/material/DialogActions';
import IconButton from '@mui/material/IconButton';
import CloseIcon from '@mui/icons-material/Close';
import Typography from '@mui/material/Typography';
import './filter.css'
import { Slider, FormControl,FormLabel,FormControlLabel,Radio,RadioGroup } from '@mui/material';
const BootstrapDialog = styled(Dialog)(({ theme }) => ({
    '& .MuiDialogContent-root': {
        padding: theme.spacing(2),
    },
    '& .MuiDialogActions-root': {
        padding: theme.spacing(1),
    },
}));

function BootstrapDialogTitle(props) {
    const { children, onClose, ...other } = props;

    return (
        <DialogTitle sx={{ m: 0, p: 2 }} {...other}>
            {children}
            {onClose ? (
                <IconButton
                    aria-label="close"
                    onClick={onClose}
                    sx={{
                        position: 'absolute',
                        right: 8,
                        top: 8,
                        color: (theme) => theme.palette.grey[500],
                    }}
                >
                    <CloseIcon />
                </IconButton>
            ) : null}
        </DialogTitle>
    );
}

BootstrapDialogTitle.propTypes = {
    children: PropTypes.node,
    onClose: PropTypes.func.isRequired,
};

export default function CustomizedDialogs() {
    const [open, setOpen] = React.useState(false);

    const handleClickOpen = () => {
        setOpen(true);
    };
    const handleClose = () => {
        setOpen(false);
    };

    return (
        <div>
            <Button variant="outlined" onClick={handleClickOpen} className='filter1'>
                <i class="fi fi-rr-settings-sliders"></i>
                <p>filters</p>
            </Button>
            <BootstrapDialog
                onClose={handleClose}
                aria-labelledby="customized-dialog-title"
                open={open}
            >
                <BootstrapDialogTitle id="customized-dialog-title" onClose={handleClose}>
                    Filters
                </BootstrapDialogTitle>
                <DialogContent dividers>
                    <Typography gutterBottom>
                        <p> Select Range </p>
                        <Slider defaultValue={0} aria-label="Default" valueLabelDisplay="auto" />
                    </Typography>
                    <Typography gutterBottom>
                        <FormControl>
                            <FormLabel id="demo-radio-buttons-group-label">Sort By</FormLabel>
                            <RadioGroup
                                aria-labelledby="demo-radio-buttons-group-label"
                                defaultValue="Popularity"
                                name="radio-buttons-group"
                            >
                                <FormControlLabel value="Popularity" control={<Radio />} label="Popularity" />
                                <FormControlLabel value="Rating: High to low" control={<Radio />} label="Rating: High to low" />
                                <FormControlLabel value="Delivery time" control={<Radio />} label="Delivery time" />
                                <FormControlLabel value="Cost: Low to High" control={<Radio />} label="Cost: Low to High" />
                                <FormControlLabel value="Cost: High to Low" control={<Radio />} label="Cost: High to Low" />
                            </RadioGroup>
                        </FormControl>
                    </Typography>
                    <Typography gutterBottom>
                        Aenean lacinia bibendum nulla sed consectetur. Praesent commodo cursus
                        magna, vel scelerisque nisl consectetur et. Donec sed odio dui. Donec
                        ullamcorper nulla non metus auctor fringilla.
                    </Typography>
                </DialogContent>
                <DialogActions>
                <Button autoFocus onClick={handleClose}>
                        Clear
                    </Button>
                    <Button autoFocus onClick={handleClose}>
                       Apply
                    </Button>
                </DialogActions>
            </BootstrapDialog>
            <Button className='btn1' variant="outlined"><p>Rating 4.0+</p></Button>
        </div>
    );
}