import React, { useState } from 'react'
import Header from '../../components/common/header/header'
import TabOption from '../../components/common/tabOption/tabOption'
import Footer from '../../components/common/footer/footer'
import Delivery from '../../components/common/delivery/delivery';
// import DinningOut from '../../components/common/dinningOut';
import Nightlife from '../../components/common/nightLife/nightlife';
// import Pagination from '../../components/common/pagination';
// import CustomTabPanel from '../../components/common/pagination'
// import BootstrapDialogTitle from '../../components/common/filter'
import DinningOut from '../../components/common/dinningOut/dinningOut';

const HomePage = () => {

    const [activeTab, setActiveTab] = useState("Delivery")
    return (
        <div>
            <Header />
            <TabOption activeTab={activeTab} setActiveTab={setActiveTab} />

            {/* <CustomTabPanel/> */}
            {getCorrectScreen(activeTab)}
            <Footer />
        </div>
    );
};

const getCorrectScreen = (tab) => {

    switch (tab) {
        case "Delivery":
            return <Delivery/>;
        case "Dinning Out":
            return <DinningOut/>;
        case "Nightlife":
            return <Nightlife/>;
            default:
                return <Delivery/>;
    }
};

export default HomePage
