import React from 'react'
import './tab.css'
import './media-tab.css'


const TabOption = ({activeTab,setActiveTab}) => {
  return (
    <>
    <div>
{/* <button onClick={() => setActiveTab("Nightlife")}>click me</button> */}
    </div>
    <div className="main">
      <div className="left">
      <div className='delivery-tab'>
      <div className='img-border'> <img src="https://b.zmtcdn.com/data/o2_assets/c0bb85d3a6347b2ec070a8db694588261616149578.png?output-format=webp" alt="error" />
      </div>
      {/* <p>Delivery</p> */}
      <button onClick={() => {
        setActiveTab("Delivery")
        document.getElementById("pink-line").style.marginLeft="16vw";
      }
      }>Delivery</button>
    </div>
      </div>
      <div className="center">
      <div className='delivery-tab'>
      <div className='img-border'> <img src="https://b.zmtcdn.com/data/o2_assets/78d25215ff4c1299578ed36eefd5f39d1616149985.png?output-format=webp" alt="error" />
      </div>
      {/* <p>Dinning Out</p> */}
      <button onClick={() => {
        setActiveTab("Dinning Out")
        document.getElementById("pink-line").style.marginLeft="32vw";
      }
      }>Dinning out</button>
    </div>
      </div>
      <div className="right">
      <div className='delivery-tab'>
      <div className='img-border'> <img src="https://b.zmtcdn.com/data/o2_assets/01040767e4943c398e38e3592bb1ba8a1616150142.png" alt="error" />
      </div>
      {/* <p>Night Life</p> */}
      <button onClick={() =>{
       setActiveTab("Nightlife")
       document.getElementById("pink-line").style.marginLeft="50vw";
      }
    }>Night Life</button>
    </div>
      </div>
    </div>
    <div className="pink-line" id='pink-line'></div>
    <hr className="hr" />
    </>
  )
}

export default TabOption
 