import React from 'react'
import "./header.css"
import "./media-home.css"
import {
    Grid,
    Paper,
    TextField,
    Link,
    Breadcrumbs
} from '@mui/material'
import FmdGoodSharpIcon from '@mui/icons-material/FmdGoodSharp';
const Header = () => {
    return (
        <>
            <div className='flex'>
                <div className=' logo'>
                    <img src="https://b.zmtcdn.com/web_assets/b40b97e677bc7b2ca77c58c61db266fe1603954218.png" alt="Error" className='header-Logo' />
                </div>
                <div>
                    <Grid  className='flex' id="live" >
                        <Grid item xs={6} className='flex' id="flex">
                            <Paper elevation={3} className='paper'>
                                <FmdGoodSharpIcon className='location' />
                                <TextField id="standard-basic" className='textfield' label="" variant="standard" placeholder='Enter You Location' />
                                <div className='verticalline'></div>
                                <svg xmlns="http://www.w3.org/2000/svg" fill="#828282" width="18" height="18" viewBox="0 0 20 20" aria-labelledby="icon-svg-title- icon-svg-desc-" role="img" class="sc-rbbb40-0 iwHbVQ"><title>Search</title><path d="M19.78 19.12l-3.88-3.9c1.28-1.6 2.080-3.6 2.080-5.8 0-5-3.98-9-8.98-9s-9 4-9 9c0 5 4 9 9 9 2.2 0 4.2-0.8 5.8-2.1l3.88 3.9c0.1 0.1 0.3 0.2 0.5 0.2s0.4-0.1 0.5-0.2c0.4-0.3 0.4-0.8 0.1-1.1zM1.5 9.42c0-4.1 3.4-7.5 7.5-7.5s7.48 3.4 7.48 7.5-3.38 7.5-7.48 7.5c-4.1 0-7.5-3.4-7.5-7.5z"></path></svg>
                                <TextField id="standard-basic" className='textfield' label="" variant="standard" placeholder='Search for restaurant' />
                            </Paper>
                            <div className='login'>
                                <div className='log'>Log in</div>
                                <div className='sign'>Sign up</div>
                            </div>
                        </Grid>
                    </Grid>
                </div>
            </div>
            <div id="element">
                <Breadcrumbs className='breads' aria-label="breadcrumb">
  <Link underline="hover" color="text.primary" href="/">
    Home
  </Link>
  <Link
    underline="hover"
    color="text.primary"
    href="/material-ui/getting-started/installation/"
  >
    India
  </Link>
  <Link underline="hover" color="inherit">Delhi NCR Restaurants</Link>
</Breadcrumbs>
                </div>
        </>
    )
}

export default Header
